#root {
  display: flex;
  flex-flow: column;
  flex-direction: column;
  height: 100vh;
  /* color: #ff7700; */
  color: #005073;
}

.full-height {
  height: 100%;
}

.App {
  text-align: center;
}

#loading {
  text-align: center;
}

header {
  font-size: 32px;
}

#main-content {
  flex: 1 1 auto;
  overflow: auto;
  /* background-color: #e6b52f; */
  /* background-color: #379be5b8 */

  /* Here we will have some themes testing */
  /* background: #71c7ec; */
  background: #dfdfdf;
}

