.tred {
  background:#ec9a718c !important;
}

.tblue {
  background: #71c7ec9e !important;
}

.tgreen {
  background: #61a9537a !important;
}

.tyellow {
  background: #f7bc0f6e !important;
}

.center {
  text-align: center;
}

.gallery-item {
  width: 100%;
  margin: 20px 0;
  text-align: center;
}

#prediction-gallery {
  margin-bottom: 12px;
}

#prediction-gallery-row {
  height: 80vh;
  overflow: auto;
  margin-top: 8px;
}

.tabs {
  margin-right: 2%;
  margin-bottom: 2%;
  margin-top: 2%;
}

#questions-tab {
  margin: 12px;
  width: 95%;
}

.simple-player-card {
  min-width: 200px;
  margin-top: 24px;
}

.height-in-percent {
  height: 92%;
}

.input-gap {
  margin: 12px 0;
}

.not-sold-player {
  opacity: 1;
}

.sold-player {
  opacity: 0.5;
}

